



.listTitle {
	font-size: 2rem;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
	padding: 0 0.8rem;
	color: #FFF;
}


.movieList {
	padding: 0;
	list-style: none;
	color: #FFF;
}

.movieList > a {
	text-decoration: none;
	color: inherit;
}

.movieList li {
	margin: 1rem;
	display: inline-block;
	position: relative;
	text-align: center;
}

.movieList p {
	margin: 0;
	position: absolute;
	overflow: hidden;
	left: 0.2em;
	right: 0.2em;
	top: 0em;
	bottom: 0;
	z-index: 1;
	font-size: 1.1rem;
	text-shadow: 0 0 0.2em black;
	font-weight: bold;
}

.movieList img {
	width: 12rem;
	height: 12rem;
	object-fit: cover;
}
