.app {
	text-align: center;
	background: #111;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1;
	display: flex;
	flex-direction: column;
}


.header {
	position: sticky;
	top: 0;
	background-color: #701;
	display: flex;
	align-items: center;
	justify-content: left;
	color: #ddd;
	flex-wrap: wrap;
	font-family:  "Arial Black", sans-serif;
	z-index: 1;
}

.header h1 {
	font-size: 1rem;
}


.logo {
	height: 2rem;
	margin: 0.1rem 0.55rem;
	filter: saturate(0%) brightness(630%) contrast(300%);
}


.headerMenu {
	margin-left: auto;
	display: block;
	align-items: right;
	text-align: right;
	height: 3rem;
}
.headerMenu > p {
	display: inline-block;
	height: 3rem;
	width: 3rem;
	margin: 0;
}
.headerMenu a {
	height: 3rem;
	position: relative;
	display: block;
}
.headerMenu img {
	height: 50%;
	width: 50%;
	margin: auto;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

.activeLink {
	background: #111;
}

.headerMenu a:not(.activeLink) img {
	filter: saturate(0%) brightness(0%) invert(95%);
}

