
.movieDetails {
	background-size: cover;
	background-blend-mode: darken;
	color: #FFF;
	flex: 1;
	padding: 1.5rem 1.5rem 1rem;
	display: block;
}


.movieDetails h2 {
	font-size: 2.3rem;
	margin-bottom: 0;
	margin-top: 0;
}

.posterAndInfoContainer {
	background: rgba(0, 0, 0, 0.2);
	border: 1px solid transparent;
	border-radius: 10px;
	flex: 1;
	display: grid;
	margin: 0 auto;
	max-width: 70rem;
	grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
	gap: 2em;
}
.posterAndInfoContainer > div {
	text-shadow: 0px 0px 2px #000;
	position: relative;
	width: 100%;
	display: flex;
	flex-direction: column;
}
.tagline {
	font-size: 1.4rem;
	margin-top: 0;
	color: #AAA;
}
.poster {
	width: 100%;
	object-fit: contain;
	max-height: 90vh;
	max-width: 90vw;
}
.trailer {
	display: block;
	width: auto;
	border: none;
	flex: 1;
	flex-grow: 1;
	min-width: 14rem;
	min-height: 12rem;
}

.genres {
	display: block;
	padding: 0;
}

.genres > li {
	display: inline-block;
	margin: 0 0.6em 0.2em 0.6em;
	padding: 0;
	color: #777;
}
.overview {
	font-size: 1.35rem;
	max-height: 11em;
	margin-top: 0;
	margin-bottom: 1.4em;
	overflow-y: auto;
}

.buttonRow {
	text-align: right;
}
.buttonRow > button {
	background: none;
	margin: 0.5rem 0.4rem 0;
	padding: 0;
	border: none;
	width: 3rem;
	height: 3rem;
	filter: saturate(0%) brightness(500%);
}
.buttonRow > button[data-s] {
	width: 100%;
	height: 100%;
}
.buttonRow > button > img {
	width: 100%;
	height: 100%;
}
.buttonRow > button:active {
	transform: translate(1px, 1px);
}
.buttonRow > button:focus {
	outline: 1px dashed rgba(200, 0, 0, 0.3);
}

.buttonRow > button[data-inlist=true] {
	filter: saturate(0%) brightness(900%) drop-shadow(0 0 3px red);
}

