


.searchInput {
	margin: 1.5rem 0;
	width: calc(100% - 3rem);
	min-width: 10rem;
	box-sizing: border-box;
	border: 2px outset #701;
	border-radius: 0.4em;
	height: 3rem;
	text-align: center;
	background: #DDD;
	min-width: 10em;
	font-size: 1.5rem;
}

.filmList {
	margin: 0;
	padding: 0 2em;
	display: grid;
	gap: 1em;
	grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
	text-align: center;
	color: #FFF;
}

.filmList > li {
	background: #0A0A0A;
	border: 1px solid #0A0A0A;
	border-radius: 0.8rem;
	animation: fadeIn 0.2s;
	display: flex;
	flex-direction: column;
	align-content: flex-start;
	justify-content: flex-start;
}




.filmList h2 {
	font-size: 1rem;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
	padding: 0 0.8rem;
}


.filmList a {
	color: inherit;
	text-decoration: inherit;
	display: contents;
	
}
.filmList p {
	font-size: 0.9rem;
	hyphens: auto;
	margin: 0.7em 1.2em 0.7em;
	text-align: left;
	color: #AAA;
	min-height: 5rem;
	text-align: justify;
}

.poster {
	display: block;
	width: calc(100% - 2rem);
	margin: auto;
	min-width: 6rem;
	min-height: 8rem;
	max-height: 45rem;
	object-fit: contain;
	animation: fadeIn 1s;
}

@keyframes fadeIn {
    from {
        opacity: 0;
	}
    to {
        opacity: 1;
    }
}


.loadingText {
	color: #DDD;
    animation: fadeInLoadingText 4.5s;
}

@keyframes fadeInLoadingText {
    from {
        opacity: 0;
    }
    50% {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

